export default {
	data() {
		return {
			loadStatus: "more",
			pageNum: 1,
			pageSize: 10,
			total: "",
			list: [],
			myEmployeeId:"",
			searchVal: "",
		}
	},
	onLoad() {
		this.clearList();
		this.getList(1);
	},
	methods: {
		reload(){
			this.clearList();
			this.getList(1);	
		},
		statusPicker: function(e) {
			const that = this;
			that.status = that.statusArr[e.detail.value];
		},
		
		getList(startPage) {
			const that = this;
			let param = {
				pageNo: startPage,
				pageSize: that.pageSize
			}
			that.loadStatus = "loading";
			that.$api.noticeListC(param).then(res => {
				if (res.success) {
					let result = res.datas.rows;
		
					for (var i in result) {
						result[i].createEmployeeHeadImg = result[i].createEmployeeHeadImg?result[i].createEmployeeHeadImg:"../../../../static/images/defaultUserPic.png";
						that.list.push(result[i]);
					}
					let total = res.datas.total;
					that.total = total;
					let totalPageNum = Math.ceil(total / that.pageSize);
					if (parseInt(totalPageNum) > parseInt(that.pageNum)) {
						that.pageNum++;
						that.loadStatus = "more";
					} else {
						that.loadStatus = "noMore";
					}
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
			})
		},
		gotoDetail(id){
			const that = this;
			that.$Router.push({
				path:"/pages/my/manageNavs/notice/detail",
				query:{
					id:id
				}
			})
		},
		clearList() {
			const that = this;
			that.pageNum = 1;
			that.total = "";
			that.list = [];
		},
		search() {
			const that = this;
			that.clearList();
			that.getList(1);
		}
	}
}
